import React, { Component } from 'react';
import { Table } from 'antd';
import { BtnDelete } from 'components/common/button';
import { setStudentNotEligibleExam } from 'dump/data';

const columns = [
  {
    key: 'operation',
    title: 'Thao tác',
    dataIndex: 'operation',
    render: () => <BtnDelete />,
  },
  {
    title: 'Mã sinh viên',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Họ và tên',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'birthdate',
    key: 'birthdate',
  },
  {
    title: 'Tên lớp',
    dataIndex: 'class',
    key: 'class',
  },
  {
    title: 'Tên môn',
    dataIndex: 'courseName',
    key: 'courseName',
  },
  {
    title: 'Lý do',
    dataIndex: 'reasonNotEligibleExam',
    key: 'reasonNotEligibleExam',
  },
];

class TableStudentChecked extends Component {
  state = {};
  render() {
    return <Table bordered dataSource={setStudentNotEligibleExam} columns={columns} />;
  }
}

export default TableStudentChecked;



