import React, { Component } from 'react';
import { Table, Button } from 'antd';
import { setStudentNotEligibleExam } from 'dump/data';

const columns = [
  {
    title: 'Thao tác',
    dataIndex: 'operation',
    render: () => (<Button type="primary">Thêm</Button>)
  },
  {
    title: 'Mã sinh viên',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Họ và tên',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'birthdate',
    key: 'birthdate',
  },
  {
    title: 'Tên lớp',
    dataIndex: 'class',
    key: 'class',
  },
  {
    title: 'Tên môn',
    dataIndex: 'courseName',
    key: 'courseName',
  },
  {
    title: 'Lý do',
    dataIndex: 'reasonNotEligibleExam',
    key: 'reasonNotEligibleExam',
  }
];

class TableStudentNotChecked extends Component {
  state = {};
  render() {
    return (
      <Table bordered dataSource={setStudentNotEligibleExam} columns={columns} />
    );
  }
}

export default TableStudentNotChecked;



