import React, { Component } from 'react';
import { Row, Col, Menu, Icon } from 'antd';
import './style.scss';

class MenuTabTop extends Component {
  state = {
    current: 'printTranscript',
  };
  handleClick = e => {
    console.log('click ', e);
    this.setState({
      current: e.key,
    });
  };
  render() {
    return (
      <Row>
        <Col span={24}>
          <Menu
            style={{ textAlign: 'right' }}
            onClick={this.handleClick}
            selectedKeys={[this.state.current]}
            mode="horizontal"
          >
            <Menu.Item key="printListNotEligible">
              <Icon type="printer" /> In danh sách KDĐK thi
            </Menu.Item>
            <Menu.Item key="fileExcel">
              <Icon type="file-excel" /> Xuất Excel
            </Menu.Item>
          </Menu>
        </Col>
      </Row>
    );
  }
}

export default MenuTabTop;



